const initial = {
  list: [],
  collection_info: {},
  user_id: ""
};

const index = (state = initial, action) => {
  switch (action.type) {
    case "SET_TEACHER_QUESTION":
      let res = Object.assign({}, state, action.value);
      return res;
    default:
      return state;
  }
};
export default index;
